.advantages__title {
    font-size: 14px;
    line-height: 26px;
    font-weight: 400;
    width: 285px;
    text-align: center;
    margin: 0 auto;
    margin-top: 40px;
    margin-bottom: 27px;
}

.advantages__list {
    list-style: none;
    width: 320px;
}

.advantages__item {
    text-align: center;
    position: relative;
    
    &:nth-child(3)::after,
    &:nth-child(4)::after {
        content: "";
        width: 280px;
        height: 1px;
        position: absolute;
        top: 166px;
        left: 20px;
        background-color: #D6D6D6;
    }
}

.advantages__item:nth-child(3),
.advantages__item:nth-child(4),
.advantages__item:nth-child(5) {
    background-color: #EEEEEE;
    // height: 172px;
}

