.advantages__description {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    position: relative;
}

.advantages__description--picture {
    background-color: #81B3D2;
    color: #fff;
}

.advantages__img {
    display: block;
}

.advantages__header {
    font-size: 18px;
    line-height: 26px;
    padding-top: 26px;
}

.advantages__number::before,
.advantages__number::after {
    content: "";
    width: 10px;
    height: 1px;
    // background-color: #fff;
    background-color: #000;
    position: absolute;
    top: 76px;
}

.advantages__number::before {
    content: "";
    left: 134px;
}

.advantages__number::after {
    content: "";
    left: 176px;
}


